.hljs{color:#A9B7C6;background:#282b2e;display:block;overflow-x:auto;padding:.5em;webkit-text-size-adjust:none}
.hljs-number{color:#6897BB}
.hljs-deletion,.hljs-keyword{color:#CC7832}
.hljs-javadoc{color:#629755}
.hljs-comment{color:grey}
.hljs-annotation{color:#BBB529}
.hljs-addition,.hljs-string{color:#6A8759}
.hljs-change,.hljs-function .hljs-title{color:#FFC66D}
.hljs-doctype,.hljs-tag .hljs-title{color:#E8BF6A}
.hljs-tag .hljs-attribute{color:#BABABA}
.hljs-tag .hljs-value{color:#A5C261}